.logo {
  width: fit-content;
  height: 30em;
  &:hover {
    transform: scale(1.1);
  }
  animation: mymove 10s infinite;
  position: absolute;
}

@media screen and (max-width: 990px) {
    .logo{
        display: none;
    }
}

@keyframes mymove {
  0% {
    left: 45%;
    top: 40%;
  }

  50% {
    left: 50%;
    top: 30%;
  }

  100% {
    left: 45%;
    top: 40%;
  }
}

.runner::before {
  transform: scaleX(0);
  transform-origin: bottom right;
}

.runner:hover::before {
  transform: scaleX(1);
  transform-origin: bottom left;
  cursor: pointer;
}

.runner::before {
  content: " ";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  inset: 0 0 0 0;
  background: #e5e1f4;
  z-index: -1;
  transition: transform 0.3s ease;
}

.runner {
  position: relative;
  font-size: 5rem;
  cursor: pointer;
}

.content {
  position: absolute;
  top: 50%;
  left: 30%;
  transform: translate(-50%, -50%);
  overflow: hidden;
  font-family: "Lato", sans-serif;
  font-size: 35px;
  color: #000000;
  display: flex;
  align-content: center;
  justify-content: center;
  padding: 0;
}

.content__container {
  font-weight: 600;
  overflow: hidden;
  height: 58px;
  padding: 0 40px;
  display: inline-block;
  margin-top: auto;
  margin-bottom: 0;
}

.content::before {
  content: "[";
  font-size: 60px;
}

.content::after {
  content: "]";
  right: 0;
  font-size: 60px;
}

.content::after .content::before {
  position: absolute;
  top: 0;

  color: #16a085;
  font-size: 42px;
  line-height: 40px;

  -webkit-animation-name: opacity;
  -webkit-animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
  animation-name: opacity;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

.content__text {
  display: inline;
  float: left;
  margin: 0;
}

.content__container__list {
  margin-top: 0;
  text-align: left;
  list-style: none;

  -webkit-animation-name: change;
  -webkit-animation-duration: 10s;
  -webkit-animation-iteration-count: infinite;
  animation-name: change;
  animation-duration: 10s;
  animation-iteration-count: infinite;
}

.content__container__list__item {
  line-height: 30px;
  margin: 25px;
  padding: 0px;
}

@-webkit-keyframes opacity {
  0%,
  100% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }
}

@-webkit-keyframes change {
  0%,
  12.66%,
  100% {
    transform: translate3d(0, 0, 0);
  }

  16.66%,
  29.32% {
    transform: translate3d(0, -25%, 0);
  }

  33.32%,
  45.98% {
    transform: translate3d(0, -50%, 0);
  }

  49.98%,
  62.64% {
    transform: translate3d(0, -75%, 0);
  }

  66.64%,
  79.3% {
    transform: translate3d(0, -50%, 0);
  }

  83.3%,
  95.96% {
    transform: translate3d(0, -25%, 0);
  }
}

